import swiperTabHead from "../../components/index/swiper-tab-head.vue";
import loadMore from "../../components/common/load-more.vue";
import noThing from "../../components/common/no-thing.vue";
import billgoryList from "../../components/bill/billgory-list.vue";
import billData from "../../components/bill/bill-data.vue";
export default {
	components:{
		swiperTabHead,
		loadMore,
		noThing,
		billgoryList,
		billData
	},
	data() {
		return {
			todaydata:[
				{ name:"今日支出", num:0 },
				{ name:"今日收入", num:0 },
				{ name:"今日报销", num:0 },
				{ name:"今日借贷", num:0 },
			],
			swiperheight:800,
			tabIndex:0,
			todayrecord:[]
		}
	},
	onLoad(e) {
		this.__init(e.userid);
	},
	methods: {
		// tabbar点击事件
		tabtap(index){
			this.tabIndex=index;
		},
		// 滑动事件
		tabChange(e){
			this.tabIndex=e.detail.current;
		},
		__init(userid){
			this.gettodayList();
			// 初始化统计数据
			this.getCounts(userid);
			// 开启监听
			uni.$on('updateData',this.updateData);
		},
		// 初始化统计数据
		async getCounts(id){
			let counts;
			let [err,res] =await this.$http.get('/user/getcounts/'+id);
			if (!this.$http.errorCheck(err,res)) return;
			counts = res.data.data;
			// console.log(counts)
			if (counts) {
				this.todaydata[0].num = counts.zc_today_sum;
				this.todaydata[1].num = counts.sr_today_sum;
				this.todaydata[2].num = counts.bx_today_sum;
				this.todaydata[3].num = counts.xyk_today_sum;
			}
		},
		// 获取今日账单列表
		async gettodayList(){
			let [err,res] = await this.$http.get(`/user/todayrecord`,{},{ token:true });
			if (!this.$http.errorCheck(err,res)) return;
			console.log(res.data.data);
			let list = res.data.data.list;
			this.todayrecord=  list;
		}
	}
}
